.SignupPage {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.inputBox {
  width: 100%;
  position: relative;
}

.error-mark {
  width: 20px;
  height: 20px;
  background: url("data:image/svg+xml;base64,");
  position: absolute;
  top: 10px;
  left: 300px;
}

.success-mark {
  width: 20px;
  height: 20px;
  background: url("data:image/svg+xml;base64,");
  position: absolute;
  top: 10px;
  left: 300px;
  z-index: 2;
}
